const baseDashboardFields = arrayClean(['id', 'displayName', 'displayDescription', 'favorite~rename(starred)', 'access', 'restrictFilters', 'allowedFilters', 'layout', 'itemConfig', "dashboardItems[".concat(getDashboardItemsFields().join(','), "]")]);
