export const apiFetchDashboard = async function (dataEngine, id) {
  let {
    mode = null,
    forSave = false
  } = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  const query = isViewMode(mode) ? viewDashboardQuery : editDashboardQuery;

  try {
    const dashboardData = await dataEngine.query({
      dashboard: query
    }, {
      variables: {
        id
      }
    });
    const dashboard = dashboardData.dashboard;

    if (!forSave) {
      return getCustomDashboards(Object.assign({}, dashboard, {
        dashboardItems: withShape(dashboard.dashboardItems)
      }))[0];
    }

    return dashboard;
  } catch (error) {
    console.log('Error: ', error);
  }
};